import axios from 'axios'
const baseUrl = '/api/persons'

const getAll = () =>
  axios
    .get(baseUrl)
    .then(res => res.data)

const create = (newObject) =>
  axios
    .post(baseUrl, newObject)
    .then(res => res.data)

const destroy = (id) =>
  axios
    .delete(`${baseUrl}/${id}`)
    .then(res => res.data)

const update = (id, changedPerson) =>
  axios
    .put(`${baseUrl}/${id}`, changedPerson)
    .then(res => res.data)

export default {getAll, create, destroy, update}



